@mixin desktop{
    @media (min-width: 1100px){
        @content;
    }
}

@mixin desktop-tablet{
    @media (min-width: 550px){
        @content;
    }
}

@mixin tablet{
    @media (min-width:550px) and (max-width: 1099px){
        @content;
    }
}

@mixin tablet-mobile{
    @media  (max-width: 1099px){
        @content;
    }
}

@mixin mobile{
    @media (max-width:549px){
        @content;
    }
}
